.App {
  text-align: center;
}

.me {
  /* animation: App-logo-spin infinite 20s linear; */
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: cadetblue;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  font-family: 'Montserrat', sans-serif;
  color: #002447;
  font-weight: 400;
}

h1 {
  font-family: 'Poppins', serif;
  /* font-family: 'Yeseva One', cursive; */
  font-weight: 700;
  color: #feb249;
  display: block;
}

.title {
  margin: 0;
}
p {
  font-weight: 500;
  font-size: 20px;
  line-height: 23px;
  color: white;
}

a {
  color: white;
  background-color: transparent;
}
a:hover {
  color: #002447;
}
.page {
  height: 100vh;
  display: flex;
  align-items: center;
}

.content-page {
  flex-direction: column;
  justify-content: center;
}

.resume-align {
  flex-direction: column;
  flex-wrap: no-wrap;
  justify-content: center;
}

.about-me-paragraph {
  width: 400px;
  display: flex;
  align-content: center;
  justify-content: center;
  flex-wrap: wrap;
}

.header-bg {
  background-color:#002447;
  border-radius: 5px;
  padding: 0 15px;
}

.resume-box-header {
  margin-bottom: 0;
}

.navbar-links {
  padding-top: 0;
  padding-bottom: 3px;
}

.hide-nav {
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  opacity: 0;
  cursor: pointer;
}

.show-nav {
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  opacity: 1;
  cursor: pointer;
}

.resume-box-wrapper {
  display: flex;
}

