.expand-resume-box {
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  opacity: 1;
  width: 400px;
  height: 100%;
  border-radius: 5px;
  padding: 5px;
  background-color: #002447;
  margin: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  cursor: pointer;
}

.contract-resume-box {
  -webkit-transition: 1s;
  -moz-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  opacity: 1;
  width: 400px;
  height: 60px;
  border-radius: 5px;
  padding: 5px;
  background-color: #002447;
  margin: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  cursor: pointer;
}

.resume-box {
  width: 350px;
  height: 60px;
  border-radius: 5px;
  background-color: #002447;
  margin: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.show-resume-p {
  -webkit-transition: .5s;
  -moz-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
  opacity: 1;
  opacity: 1;
  height: 1;
  font-size: 12px;
  margin-top: 5px;
}

.hide-resume-p {
  -webkit-transition: .5s;
  -moz-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
  opacity: 0;
  height: 0;
  margin: 0;
  font-size: 12px;
  margin-top: 5px;
}

h2 {
  font-size: 12px;
  text-decoration: underline;
}

h3 {
  font-size: 12px;
  font-style: italic;
}

.nav-container {
  z-index: 100;
}